html, body {
  height: 100%;
  margin: 0;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
}

.zagal {
  font-family: "Montserrat", sans-serif;
  font-weight: bold;
}

.block {
  background-color: #d6d6ed;
  border-radius: 8px;
  width: 400px;
  height: 280px;
  justify-content: center;
  display: flex;
  flex-direction: column;
  text-align: center;
}

